import React from 'react'
import Logo from './Logo'

class Logos extends React.Component {

  constructor(props) {
    super(props);
    this.state = { selections: [] };
    this.buildChildren = this.buildChildren.bind(this);
  }

  onChildToggle(id, selected) {
    var selections = [];
    selections[id] = selected;

    this.setState({
      selections: selections
    });
  }

  buildChildren(logo, index) {
    return <Logo key={logo.name} logo={logo} maxsize="3" selected={this.state.selections[logo.name]} handleClick={this.onChildToggle.bind(this)} />
  }


  render() {
    var logos = [
      { 'name': 'The Learn Dock - education consultancy', 'image': './images/learndock.png'  },
      { 'name': 'Home Body Pilates - pilates instructor', 'image': './images/homebodypilates.png'  },
      { 'name': 'Running Miss - running blogger', 'image': './images/runningmiss.png'  },
      { 'name': 'Pikalion - young football fan', 'image': './images/pikalion.png'  },
      { 'name': 'Grounded - speciality coffee', 'image': './images/grounded.png' },
      { 'name': 'Open Source Tester - IT consultant', 'image': './images/ost.png'  },
      { 'name': 'Runaround - running shop', 'image': './images/runaround.png'  },
      { 'name': 'Solid Sounds - soul DJ', 'image': './images/solidsounds.png'  },
      { 'name': 'Platform J - retro games enthusiast', 'image': './images/platformj.png'  }
    ];


    return  <div className="pure-g"> {logos.map(this.buildChildren)} </div>
  }
}

export default Logos;
