import React from 'react'
import Logo from './Logo'

class Arts extends React.Component {

  constructor(props) {
    super(props);
    this.state = { selections: [] };
    this.buildChildren = this.buildChildren.bind(this);
  }

  onChildToggle(id, selected) {
    var selections = [];
    selections[id] = selected;

    this.setState({
      selections: selections
    });
  }

  buildChildren(logo, index) {
    return <Logo key={logo.name} logo={logo} maxsize="2" selected={this.state.selections[logo.name]} handleClick={this.onChildToggle.bind(this)} />
  }


  render() {
    var logos = [
      { 'name': 'Tower Blocks - digital artwork', 'image': './images/towerblocksmayow.png'  },
      { 'name': 'Pavilion - digital artwork', 'image': './images/pavillionmayow.png'  },
      { 'name': 'Fountain - digital artwork', 'image': './images/fountainmayow.png'  },
      { 'name': 'First Swifts - competition piece', 'image': './images/firstswifts.png'  },
      { 'name': 'About the Blue - digital artwork', 'image': './images/abouttheblue.png'  },
      { 'name': 'Dual Core - competition piece', 'image': './images/dualcore.png'  }
    ];

    return  <div className="pure-g"> {logos.map(this.buildChildren)} </div>
  }
}

export default Arts;
